body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: auto;
  background-color: #e5e5e5;
}

.navbar {
  background: linear-gradient(
    90deg,
    #f15a22 0%,
    #e5a923 26.56%,
    #e2e41a 50%,
    #87c879 72.92%,
    #4eb2c5 100%
  );
  height: 56px;
  padding: 0 !important;
  padding-left: 24px !important;
  padding-right: 32px !important;
}

.navbar-brand {
  display: flex !important;
  margin-right: 24px !important;
}

.navbar-logo {
  width: 32px;
  height: 32px;
  margin-right: 32px !important;
}

.div-bar {
  border: 1px solid #fff;
}

.active {
  border-bottom: 4px solid #fff;
}

.nav-link {
  display: flex !important;
  align-items: center;
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  text-align: right;
  color: #ffffff !important;
}

.navbar-text {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  text-align: right;
  color: #ffffff !important;
}

.mainnew-card {
  background: #ffffff;
  box-shadow: 0px 2px 4px rgba(41, 41, 39, 0.05),
    0px 5px 10px rgba(41, 41, 39, 0.1);
  border-radius: 0px 8px 8px 0px;
  max-height: 440px;
}

.mainnew-image {
  width: 100%;
  max-height: 440px;
  object-fit: cover;
  height: 100%;
}

.mainnew-card-text {
  font-family: Roboto;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  color: #292927;
}

@media only screen and (max-width: 1200px) {
  .mainnew-card {
    max-height: 100%;
  }
}

.mainnew-card-button {
  width: 80px;
  height: 32px;
  background: #4eb2c5;
  border-radius: 8px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: #ffffff;
  border: 1px solid #4eb2c5;
  display: flex;
  align-items: center;
  justify-content: center;
}

.mainnew-card-button:hover {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  color: rgb(243, 242, 242);
  text-decoration: none;
}

.mainnew-read-more-container {
  display: flex;
  justify-content: flex-end;
}

.mainnew-author-span {
  font-family: Roboto;
  font-style: italic;
  font-weight: 300;
  font-size: 14px;
  line-height: 16px;
  color: #6d6e71;
  margin-bottom: 24px;
}

.mainnew-card-body {
  padding: 26px 32px !important;
}

.paragraphs-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.subnew-card {
  background: #ffffff;
  box-shadow: 0px 2px 4px rgba(41, 41, 39, 0.05),
    0px 5px 10px rgba(41, 41, 39, 0.1);
  border-radius: 8px !important;
  min-width: 560px;
  max-height: 218px;
  padding: 24px 0;
}

.subnew-image {
  width: 100%;
  max-height: 170px;
  object-fit: cover;
  height: 100%;
}

.subnew-card-text {
  font-family: Roboto;
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 16px;
  color: #000000;
  height: 9em;
}

@media only screen and (max-width: 1200px) {
  .subnew-card {
    max-height: 100%;
  }
}

.subnew-card-button {
  background: #ffffff;
  border: 1px solid #4eb2c5;
  box-sizing: border-box;
  border-radius: 8px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  width: 80px;
  height: 24px;
  color: #4eb2c5;
  display: flex;
  align-items: center;
  justify-content: center;
}

.subnew-card-button:hover {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  color: rgb(41, 102, 104);
  text-decoration: none;
}

.subnew-read-more-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.subnew-author-span {
  font-family: Roboto;
  font-style: italic;
  font-weight: 300;
  font-size: 10px;
  line-height: 12px;
  color: #6d6e71;
}

.new-section {
  margin-top: 56px;
  height: calc(100% - 40px);
  padding: 48px;
  padding-top: 64px;
}

.subnew-col {
  margin-top: 16px;
}

.footer {
  background: #292927;
  height: 40px;
  padding-left: 152px !important;
}

@media only screen and (max-width: 750px) {
  .footer {
    background: #292927;
    height: 40px;
    padding: 0px !important;
    justify-content: center !important;
  }
}

.text {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #ffffff !important;
}

